import React, { useState, useEffect } from "react";

import { makeStyles, Theme } from "@material-ui/core";
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import CardHeader from '@material-ui/core/CardHeader';

import SwipeableDrawer from '@material-ui/core/SwipeableDrawer';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import FavoriteBorderIcon from '@material-ui/icons/FavoriteBorder';
import InstagramIcon from '@material-ui/icons/Instagram';
import PersonIcon from '@material-ui/icons/Person';
import Icon from "@mdi/react";
import { mdiCalendarMonth } from '@mdi/js';
import { mdiPin } from '@mdi/js';


import { useApplicationState } from '../infra/applicationState/ApplicationState';
import FollowedBandContainer from '../components/user/FollowedBandContainer';
import CalendarCotainer from "../components/user/CalendarContainer";
import EventContainer from '../components/user/EventContainer';
import InstaHighlightContainer from '../components/user/InstaHighlightContainer';

import { initTestGigs, instaPostList } from '../TempConstants';

interface UserHomeProps {

}


const useStyles = makeStyles((theme) => ({

    mobileSidePanel: {

    },

    sidepanel: {
        height: 'calc(100vh - 50px)',
        position: 'fixed',
        width: '220px',
        left: '0px',
        top: '50px',
        background: '#fff',
        [theme.breakpoints.only('xs')]: {
            display: 'none'
        }

    },

    selectedTab: {
        borderRight: '4px solid #f5003d',
        borderTopRightRadius: '4px !important',
        borderBottomRightRadius: '4px !important'
    },

    container: {
        paddingLeft: '200px',
        paddingTop: '10px',
        [theme.breakpoints.only('xs')]: {
            paddingLeft: '0px'
        }

    },

    list: {
        width: 250,
        borderTop: '3px solid blue'
    },

    selectedListItem: {
        borderRight: '4px solid #f5003d'
    },

    menuButton: {
        marginLeft: '5px',
        position: 'fixed',
        top: '50px',
        zIndex: 15,
        display: 'none',
        [theme.breakpoints.only('xs')]: {
            display: 'block'
        }
    },

    menuIcon: {
        background: 'rgb(63, 81, 181,0.8)',
        borderRadius: '4px',
        color: '#fff',
        padding: '5px'
    }

}));

const cardHeaderStyles = makeStyles(() => ({
    root: ({ ...styles }) => ({
        backgroundColor: 'rgb(63, 81, 181)',
        borderRadius: '16px',
        margin: '10px auto',
        width: '88%',
        cursor: 'pointer',
        ...styles,
    }),
    title: {
        color: '#fff',
        fontWeight: 'bold',
    },
    subheader: {
        color: 'rgba(255, 255, 255, 0.76)',
    },
}));


const UserHome: React.FC<UserHomeProps> = () => {
    const classes = useStyles();
    const mobile = useMediaQuery(useTheme().breakpoints.only('xs'));

    const { globalState } = useApplicationState();
    const [selectedTab, setSelectedTab] = useState(1);

    const [eventList, setEventList] = useState(new Array());
    const [state, setState] = React.useState(false);


    const handleTabChange = (tab: number) => {
        setSelectedTab(tab);
    }

    const toggleDrawer = (event: any) => {
        if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        setState(!state);
    };

    useEffect(() => {
        //test population of gigs
        let date = new Date();
        let gigs = initTestGigs(date.getMonth(), date.getFullYear(), "user");
        gigs = gigs.concat(initTestGigs(date.getMonth() + 1, date.getFullYear(), "user"));
        setEventList(gigs);

    }, []);



    return (
        <div>
            <div className={classes.mobileSidePanel}>
                <IconButton className={classes.menuButton} onClick={toggleDrawer} edge="start" color="inherit">
                    <MenuIcon className={classes.menuIcon} fontSize={"large"} />
                </IconButton>
                <SwipeableDrawer
                    open={state}
                    onClose={toggleDrawer}
                    onOpen={toggleDrawer}
                >
                    <div
                        className={classes.list}
                        onClick={toggleDrawer}
                        onKeyDown={toggleDrawer}
                    >
                        <List>
                            <ListItem className={selectedTab === 1 ? classes.selectedListItem : ''}
                                button onClick={() => handleTabChange(1)} >
                                <ListItemIcon><FavoriteBorderIcon /></ListItemIcon>
                                <ListItemText primary={"Followed"} />
                            </ListItem>
                            <ListItem className={selectedTab === 2 ? classes.selectedListItem : ''}
                                button onClick={() => handleTabChange(2)}>
                                <ListItemIcon><Icon path={mdiPin} size={1} /></ListItemIcon>
                                <ListItemText primary={"Events"} />
                            </ListItem>
                            <ListItem className={selectedTab === 3 ? classes.selectedListItem : ''}
                                button onClick={() => handleTabChange(3)}>
                                <ListItemIcon><Icon path={mdiCalendarMonth} size={1} /></ListItemIcon>
                                <ListItemText primary={"Calendar"} />
                            </ListItem>
                            <ListItem className={selectedTab === 4 ? classes.selectedListItem : ''}
                                button onClick={() => handleTabChange(4)}>
                                <ListItemIcon><InstagramIcon /></ListItemIcon>
                                <ListItemText primary={"Highlights"} />
                            </ListItem>
                            <ListItem className={selectedTab === 5 ? classes.selectedListItem : ''}
                                button onClick={() => handleTabChange(5)}>
                                <ListItemIcon><PersonIcon /></ListItemIcon>
                                <ListItemText primary={"Bio"} />
                            </ListItem>

                        </List>
                    </div>
                </SwipeableDrawer>
            </div>
            <div className={classes.sidepanel}>
                <CardHeader
                    className={selectedTab === 1 ? classes.selectedTab : ''}
                    classes={cardHeaderStyles()}
                    title="Followed"
                    subheader="Bands/Artists you follow"
                    onClick={() => handleTabChange(1)}
                />
                <CardHeader
                    className={selectedTab === 2 ? classes.selectedTab : ''}
                    classes={cardHeaderStyles()}
                    title="Events"
                    subheader="Your upcoming pinned events"
                    onClick={() => handleTabChange(2)}
                />
                <CardHeader
                    className={selectedTab === 3 ? classes.selectedTab : ''}
                    classes={cardHeaderStyles()}
                    title="Calendar"
                    subheader="Gig Calendar"
                    onClick={() => handleTabChange(3)}
                />
                <CardHeader
                    className={selectedTab === 4 ? classes.selectedTab : ''}
                    classes={cardHeaderStyles()}
                    title="Highlights"
                    subheader="Highlighted Insta posts"
                    onClick={() => handleTabChange(4)}
                />
                <CardHeader
                    className={selectedTab === 5 ? classes.selectedTab : ''}
                    classes={cardHeaderStyles()}
                    title="Bio"
                    subheader="Profile bio"
                    onClick={() => handleTabChange(5)}
                />
            </div>
            <div className={classes.container}>

                {selectedTab === 1 ?
                    <FollowedBandContainer list={globalState.bandList} /> : null}

                {selectedTab === 2 ?
                    <EventContainer list={eventList} /> : null}

                {selectedTab === 3 ?
                    <CalendarCotainer list={globalState.bandList} /> : null}

                {selectedTab === 4 ?
                    <InstaHighlightContainer list={instaPostList} /> : null}

                {selectedTab === 5 ?
                    <h3>Bio coming soon</h3> : null}
            </div>
        </div>
    );
}

export default UserHome;
