import React, { useState, useRef } from "react";
import clsx from 'clsx';

import { makeStyles, Theme } from "@material-ui/core";
import Grid from "@material-ui/core/Grid";
import Slide from '@material-ui/core/Slide';

import InfoCardSmall from '../InfoCardSmall';

interface FollowedBandContainerProps {
    list: any
}

const useStyles = makeStyles((theme: Theme) => ({

    root: {
        boxShadow: "0px 3px 3px -2px rgba(0,0,0,0.2), 0px 3px 4px 0px rgba(0,0,0,0.14), 0px 1px 8px 0px rgba(0,0,0,0.12)",
        borderRadius: "4px",
        backgroundColor: "#fff",
        transition: "box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;",
        margin: "20px 10px 40px 10px",
        height: 'max-content',
        padding: '20px 0',
        position: 'relative',
        minHeight: 'calc(100vh - 135px)'
    },

    gridContainer: {
        padding: '10px',
        [theme.breakpoints.only('xs')]: {
            padding: '0'
        }
    },

}));


const FollowedBandContainer: React.FC<FollowedBandContainerProps> = ({ list }) => {

    const classes = useStyles();

    return (
        <Slide direction="left" in={true} mountOnEnter unmountOnExit>
            <div className={classes.root}>

                <Grid container className={classes.gridContainer}>
                    {list.map((item: any) => <Grid xs={12} sm={6} md={4} lg={3}>
                        <InfoCardSmall
                            img={item.logo}
                            title={item.name}
                            subTitle={item.genre}
                        />
                    </Grid>)}
                </Grid>

            </div>
        </Slide>
    );
}

export default FollowedBandContainer;